import React, { useEffect, useState } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Alert from '@mui/material/Alert';
import { loadIcon } from "./LoadIcon";

const dispatchedEvents = [];

const SrsMuiAlert = function (props, ref) {
  const [IconComp, setIconComp] = useState(null);

  useEffect(() => {
    loadIcon(props.icon || "Info", setIconComp);
  }, [props.icon]);

  let componentProps = {...props, theme: props.muiTheme};
  return (
    <Alert
      {...componentProps}
      icon={IconComp && <IconComp />}
    >
      <slot />
    </Alert>
  );
}

const styles = `
  /* Custom styles for the Alert component */
`;

const props = [
  {
    name: "severity",
    defaultValue: "info",
    type: "string",
  },
  {
    name: "icon",
    defaultValue: "Info",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Alert if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAlert), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };